@use 'variables' as *;
/*кнопка бургера*/
.menu-btn a {
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 62, 121, 1);
  border-radius: 50%;
  position: relative;
  z-index: 10;
}

.header__menu-burger-button {
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 62, 121, 1);
  border-radius: 50%;
  position: relative;
  text-align: center;
}

.header_menu-burger-button__container {
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 62, 121, 1);
  border-radius: 50%;
  position: relative;
  text-align: center;
  z-index: 10;
}

/*спрятал галочку*/
#menuBurg {
  opacity: 0;
}

.header_menu-burger-button__container > .burger {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  line-height: 40px;
}

.burger-line-1 {
  position: absolute;
  top: 12px;
  left: 11px;
  width: 18px;
  height: 2px;
  background-color: #fff;
  transition: 0.5s all;
  cursor: pointer;
  z-index: 3;
}

.burger-line-2 {
  position: absolute;
  top: 19px;
  left: 11px;
  width: 10px;
  height: 2px;
  background-color: #fff;
  transition: 0.5s all;
  cursor: pointer;
  z-index: 3;
}

.burger-line-3 {
  position: absolute;
  top: 26px;
  left: 11px;
  width: 14px;
  height: 2px;
  background-color: #fff;
  transition: 0.5s all;
  cursor: pointer;
  z-index: 3;
}

.header__menu_container_burger-logo-hr:has(#menuBurg:checked)
  .header__menu_drop-menu {
  transform: translateY(0);
}

.header_menu-burger-button__container:has(#menuBurg:checked) .burger-line-1 {
  transform: translateY(7px) translateX(-2px) rotate(45deg);
  width: 21.73px;
  height: 2.17px;
}

.header_menu-burger-button__container:has(#menuBurg:checked) .burger-line-2 {
  display: none;
}

.header_menu-burger-button__container:has(#menuBurg:checked) .burger-line-3 {
  transform: translateY(-7px) translateX(-2px) rotate(-45deg);
  width: 21.73px;
  height: 2.17px;
}

#repair {
  background-image: url(../img/icon/repair.svg);
  background-repeat: no-repeat;
  background-position: right;
}
#checkstatus {
  background-image: url(../img/icon/checkstatus.png);
  background-repeat: no-repeat;
  background-position: right;
}

// кнопка показать/скрыть

.show-more-btn {
  appearance: none;
  border: none;
  font-family: 'MyFont', sans-serif;
  font-weight: 600;
  width: 134px;
  height: 24px;
  margin-top: 24px;
  margin-bottom: 34px;
  margin-left: 24px;
  background-image: url(../img/arrows/icon.svg);
  background-repeat: no-repeat;
  background-position: 15% 35%;
  background-color: inherit;
  transition: 0.5s;
  z-index: 10;
  position: relative;
}

.show-more-btn-second {
  appearance: none;
  border: none;
  font-family: 'MyFont', sans-serif;
  font-weight: 600;
  width: 134px;
  height: 24px;
  margin-top: 24px;
  margin-bottom: 34px;
  margin-left: 24px;
  background-image: url(../img/arrows/icon.svg);
  background-repeat: no-repeat;
  background-position: 15% 35%;
  background-color: inherit;
  transition: 0.5s;
  z-index: 10;
  position: relative;
}

#expand {
  background-image: url(../img/arrows/expand.svg);
  background-repeat: no-repeat;
  background-position: left;
}

.button-buys {
  background-color: $pinkBtn;
  border-radius: 16px;
  border: 0px;
  width: 136px;
  height: 32px;
  position: relative;
  text-transform: uppercase;
  font-size: 12px;
  color: $white;
  background-image: url(../img/arrows/goside.svg);
  background-repeat: no-repeat;
  background-position: 90% 50%;
  font-weight: 700;
  top: -8px;
  right: 12px;
  padding-right: 38px;
  line-height: 32px;
  font-family: 'MyFont', sans-serif;
}
